<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AddArticle') }}</span>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form lang="en" ref="AddForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-Description="{ data }">
                    <ckeditor :editor="editor" v-model="articleKeywords" :class="{ 'is-invalid': data.error != '' }">
                    </ckeditor>
                    <span class="invalid-feedback">{{ data.error }}</span>
                    <!-- <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"
                        :class="{ 'is-invalid': data.error != '' }"></ckeditor>
                    <span class="invalid-feedback">{{ data.error }}</span> -->
                    <!-- <ckeditor :editor="editor" @ready="onReady" @input="updateCkeditorModel($event, data)" :class="{ 'is-invalid': data.error != '' }"></ckeditor>
                    <span class="invalid-feedback">{{ data.error }}</span> 
                    <input type="hidden" v-model='data.fieldInfo.value' /> -->
                </template>
            </dynamic-form>
        </div>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            isLoading: false,
            articleKeywords: '',
            isAllowedFileType: null,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            placeholder: this.$t('Title'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Category'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('AssignTo'),
                            name: "AssignTo",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Category'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "FileField",
                            showLabel: true,
                            label: this.$t('ArticleAttachment'),
                            name: "ArticleAttachment",
                            value: "",
                            placeholder: "",
                            validationRules: false,
                            FieldIndex: 0,
                            config: {
                                maxSize: 10000,
                                // reject: this.AttachmentDisallowedFileType(this.disallowFileTypes),
                                showSelectedFiles: [],
                                onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: false
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ShortDescription'),
                            name: "SortDescription",
                            value: "",
                            placeholder: this.$t('ShortDescription'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "1105",
                            config: {
                                options: [],
                                onChange: this.onChangeRadio
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 4000 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "toggleField",
                            label: this.$t('VisibleToClient'),
                            name: "VisibleToClient",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-info save",
                    text: "<i class='fa fa-eye pr-2'></i> Preview",
                    onClick: this.Preview
                },
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    //onClick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.Close
                }
            ],
            fileName: null,
            file: null,
            ArticleId: 0,
            allowedPerFileSizeInMb: 0,
            disallowFileTypes: '',
            ArticleModel: {
                ArticleId: 0,
                ArticleTitle: '',
                CategoryId: '',
                StatusId: '',
                Description: '',
                SortDescription: '',
                AssignTo: '',
                VisibleToClient: '',
                fileData: [],
                ModuleName: '',
                Type: '',
                SubModuleCode: ''
            },
            StatusId: null,
            checkFileValidSize: false,
            checkFileValidExt: false,
            fileExt: '',
            fileSize: '',

        }
    },
    created: function () {
        //debugger;
        var vm = this;
        this.GetStatus();
        this.GetCategory();
        this.GetUsersByCompanyId();
        this.GetConfigurationValueByKey();
        if (this.$route.params.id > 0) {
            this.ArticleId = this.$route.params.id;
            this.GetArticleById(this.$route.params.id);
        }
        var user_id = this.GetUserInfo.ID;
        vm.FormSchema[0].Data[2].value = user_id;
    },
    methods: {
        setEditorContent(newContent) {
            debugger
            this.data.content = newContent;
        },

        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
            let mediaIcon = document.getElementsByClassName('ck-dropdown')[2]
            mediaIcon.remove();
            var divToRemove = document.querySelector('.ck-file-dialog-button');
            divToRemove.remove();
            let ArrowIcon = document.getElementsByClassName('ck ck-button ck-off')[8]
            ArrowIcon.remove();
        },
        // updateCkeditorModel: function(e, x){

        //     x.fieldInfo.value = e;
        // },
        onDeleteSelectedFiles: function (item) {
            this.$delete(this.FormSchema[0].Data[3].config, 'showSelectedFiles');
        },
        onChangeRadio: function (e, field) {
            var vm = this;
            vm.StatusId = e.target.dataset.value;
        },
        GetStatus: function () {
            var url = `type=KnowledgeBase`
            DataServices.GetDDLData(url).then(response => {
                if (response.data.DATA.length > 0) {
                    var StatusData = response.data.DATA;
                    // debugger
                    StatusData.forEach(element => {
                        var Obj = {
                            name: element.name,
                            value: element.value
                        }
                        this.FormSchema[0].Data[5].config.options.push(Obj);
                        this.$refs.AddForm.UpdateKeyValue();
                    });


                }
            })
        },
        GetCategory: function () {
            var vm = this;
            DataServices.GetCategoryDDL().then(response => {
                if (response.data.result.length > 0) {
                    var CategortData = response.data.result;
                    CategortData.forEach(element => {
                        if (element.TICKET_ARTICLE_CATEGORY_PARENT == null) {
                            let obj = {
                                name: element.TICKET_ARTICLE_CATEGORY_NAME,
                                value: element.TICKET_ARTICLE_CATEGORY_ID
                            }
                            vm.FormSchema[0].Data[1].config.options.push(obj);
                            vm.$refs.AddForm.UpdateKeyValue();
                        }
                    });
                }
            })
        },
        GetUsersByCompanyId: function () {
            var url = `moduleName=KnowledgeBase`;
            DataServices.GetUsersByCompanyId(url).then(response => {
                if (JSON.parse(response.data.result).length > 0) {
                    var StatusData = JSON.parse(response.data.result);
                    StatusData.forEach(element => {
                        var Obj = {
                            name: element.USERNAME,
                            value: element.USER_ID
                        }
                        this.FormSchema[0].Data[2].config.options.push(Obj);
                        this.$refs.AddForm.UpdateKeyValue();
                    });


                }
            })
        },
        GetConfigurationValueByKey: async function () {
            var vm = this;
            var url = `moduleName=KnowledgeBase`
            await DataServices.FrontSiteUrlHostName(url).then(function (response) {
                if (response.data.list.result.length > 0) {
                    $.each(response.data.list.result, $.proxy(function (index1, item) {
                        if (item.coreConfigKey == "AttachmentFileSize") {
                            vm.allowedPerFileSizeInMb = item.configDataValue;
                        }
                        else if (item.coreConfigKey == "AllowedFileTypes") {
                            vm.disallowFileTypes = item.configDataValue;
                            let fileTypeList = vm.AttachmentDisallowedFileType(vm.disallowFileTypes);
                            vm.FormSchema[0].Data[3].config.reject = fileTypeList;
                        }
                    }, this));
                }
            })
        },
        Preview: function () {
            var vm = this;
            vm.$router.push({ name: "ArticleDetails", params: { id: 0 } });

        },
        GetArticleById: async function (ths) {
            var vm = this;
            var url = `articleId=${ths}`
            await DataServices.GetArticleById(url).then(function (response) {
                if (response.data.status == 'Success') {
                    debugger
                    var ArticleData = JSON.parse(response.data.result);
                    vm.FormSchema[0].Data[0].value = ArticleData[0].TICKET_ARTICLE_TITLE;
                    vm.FormSchema[0].Data[1].value = ArticleData[0].TICKET_ARTICLE_CATEGORY_ID;
                    vm.FormSchema[0].Data[2].value = ArticleData[0].TICKET_ARTICLE_ASSIGNED_TO;
                    vm.articleKeywords = ArticleData[0].TICKET_ARTICLE_KEYWORDS;
                    vm.FormSchema[0].Data[5].value = ArticleData[0].STATUS_ID;
                    vm.FormSchema[2].Data[0].value = ArticleData[0].VISIBLE_TO_CLIENT;
                    var Obj = {
                        attachmentName: ArticleData[0].ATTACHMENT_NAME,
                        attachmentPath: ArticleData[0].ATTACHMENTFULLPATH
                    };
                    vm.FormSchema[0].Data[3].config.showSelectedFiles.push(Obj);
                }
                vm.fileSize = JSON.parse(ArticleData[0].ATTACHMENT_DESC).Size;
                vm.fileExt = '.' + JSON.parse(ArticleData[0].ATTACHMENT_DESC).Extension;
                var fileObj = {
                    attachement_mime: ArticleData[0].ATTACHMENT_DESC,
                    fileName: ArticleData[0].ATTACHMENT_NAME,
                    fileSize: vm.fileSize,
                    contentType: JSON.parse(ArticleData[0].ATTACHMENT_DESC).ContentType
                };
                vm.ArticleModel.fileData.push(fileObj);
            })
        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },
        SizeToMb: function (bytes) {
            // //debugger;
            var vueObj = this;
            var sizeInBuytes = parseInt(vueObj.allowedPerFileSizeInMb, 10) * 1048576;
            if (bytes > sizeInBuytes) {
                return -1;
            }
            else {
                var byesInMB = (bytes / 1024) / 1024;
                return byesInMB;
            }
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                vm.fileExt = '';
                var f = evt.target.files[0];
                var reader = new FileReader();
                reader.onload = (function (theFile) {
                    return function (e) {
                        // debugger                    
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        Close: function () {
            this.$router.push({ name: 'KnowledgebaseCAUser' });
        },
        onSubmit(formfields, val) {
            //debugger;
            var vm = this;
            vm.isLoading = true;
            var datafields = formfields;
            var extArray = vm.disallowFileTypes.split(',');
            if (extArray.includes(vm.fileExt)) {
                vm.checkFileValidExt = false;
                vm.ShowAlert(vm.$t("InvalidFileUploadMsg", vm.fileName, vm.disallowFileTypes), "warning", true, vm.$t("Alert"));
            }
            else {
                vm.checkFileValidExt = true;
            }
            var fileSizeInMB = this.SizeToMb(vm.fileSize);
            if (fileSizeInMB < parseInt(vm.allowedPerFileSizeInMb, 10)) {
                vm.checkFileValidSize = true;
            }
            else {
                vm.checkFileValidSize = false;
                vm.ShowAlert(vm.$t("ExceedAllowedSize", vm.fileName, vm.allowedPerFileSizeInMb), "warning", true, vm.$t("Alert"));
            }
            if (vm.checkFileValidExt == true && vm.checkFileValidSize == true) {
                vm.ArticleModel.ArticleId = vm.ArticleId;
                vm.ArticleModel.ArticleTitle = datafields.Title;
                vm.ArticleModel.CategoryId = datafields.Category;
                vm.ArticleModel.StatusId = vm.StatusId == null ? vm.FormSchema[0].Data[5].value : vm.StatusId;
                vm.ArticleModel.Description = datafields.Description;
                vm.ArticleModel.SortDescription = datafields.SortDescription;
                vm.ArticleModel.AssignTo = datafields.AssignTo;
                vm.ArticleModel.VisibleToClient = datafields.VisibleToClient == true ? 1 : 0;
                vm.ArticleModel.fileData = vm.fileData == undefined ? vm.ArticleModel.fileData : vm.fileData;
                vm.ArticleModel.ModuleName = 'Ticketing';
                vm.ArticleModel.Type = vm.$route.params.id > 0 ? 'UPDATE' : 'ADD'
                vm.ArticleModel.SubModuleCode = 'KnowledgeBase';
                var formData = JSON.stringify(vm.ArticleModel);
                DataServices.SaveUpdateArticle(formData).then((response) => {
                    if (response.data.status == "Success") {
                        if ((response.data.result > 0 && response.data.result != 3) && vm.ArticleModel.Type == 'ADD') {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Article')]), "Success", true, vm.$t('Alert'));
                            vm.$router.push({ name: 'KnowledgebaseCAUser' });
                        }
                        else if (response.data.result == 3 && vm.ArticleModel.Type == 'ADD') {
                            vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Article")]), "failure", true, vm.$t("Alert"));
                        }
                        else if (response.data.result > 0 && vm.ArticleModel.Type == 'UPDATE') {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Article")]), "Success", true, vm.$t("Alert"));
                            vm.$router.push({ name: 'KnowledgebaseCAUser' });
                        }
                        else if (response.data.result == 3 && vm.ArticleModel.Type == 'UPDATE') {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Article")]), "Success", true, vm.$t("Alert"));
                            vm.$router.push({ name: 'KnowledgebaseCAUser' });
                        }
                    }
                    vm.isLoading = false;
                })
            }
            else {
                vm.isLoading = false;
            }
        }
    }
}
</script>